@import url(https://unpkg.com/ress/dist/ress.min.css);
#app {
  width: 500px;
  text-align: center;
}
html,
body {
  height: 100%;
}
body {
  background-color: #f9f4ff;
}
.tabMenu {
  color: white;
}
.tabMenu li {
  background-color: #b980ff;
  padding: 10px;
  border-radius: 30px;
  margin: 8px;
  width: 100px;
  margin: 20px 0 0 20px;
  font-weight: bold;
}
.tabMenu li:hover {
  opacity: 0.5;
}
.add-button {
  position: fixed;
  right: 50px;
  bottom: 50px;
  background-color: #b980ff;
  padding: 18px;
  border-radius: 50%;
}
.add-button:hover {
  opacity: 0.5;
}
.add-button img {
  width: 30px;
}
.items li {
  width: 450px;
  height: 100px;
  background-color: white;
  text-align: center;
  padding: 30px;
  border-radius: 20px;
  list-style: none;
  margin: 20px auto;
}
.items span {
  display: wrap;
}
.isDone {
  text-decoration: line-through;
}
.taskOptions {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.tabMenu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
}
.fade-enter-active,
.fade-leave-active {
  will-change: opacity;
  -webkit-transition: opacity 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: opacity 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}
.fade-enter,
.fade-leave-to {
  opacity: 0
}
.items-part {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: white;
  -webkit-box-shadow: 0 10px 6px -6px #777;
  box-shadow: 0 10px 6px -6px #777;
  width: 300px;
  height: 350px;
  padding: 50px;
  border-radius: 20px;
  color: white;
}
.items-part input,
select {
  border: 0;
  padding: 5px;
  font-size: 12px;
  font-family: Arial, sans-serif;
  color: #aaa;
  border: solid 1px #ccc;
  margin: 0 0 10px;
}
.items-part input {
  width: 200px;
}
.items-part select {
  width: 50px;
}
.items-part label {
  display: block;
  color: black;
}
.buttons button {
  font-size: 8px;
  background-color: #b980ff;
  padding: 8px;
  border-radius: 15px;
  margin: 10px;
  width: 100%;
}
.buttons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.items {
  display: block;
}
.project-title {
  font-weight: bold;
}

